.listItem {
  border: 2px solid #dbdfea;
  color: #2e3d64;
  padding: 0.2rem 0.5rem;
  border-radius: 10px;
  margin: 1rem;
  text-align: center;
}

.listItem:hover {
  border: 2px solid #ffead2;
  cursor: pointer;
}
ul {
  list-style-type: none;
  padding: 0;
  margin: 0 0.5rem;
}
